
.sidebar[data-v-5] {
  background: black;
  display: block;
  color: white;
  position: fixed;
  left: 0;
  z-index: 500;
  box-shadow: 0 0 20px rgba(34, 36, 38, .15);
  width: auto;
  height: 100%;
  transition: all 0.5s;
  padding-top: 1.5em;
}
.togglebar[data-v-5] {
  transform: translate3d(-100%, 0, 0);
}



