
html {
  height: 100%;
}
body {
  background: #f8f9fa;
}


/*  #app {
    color: #2c3e50;
    font-family: Source Sans Pro, Helvetica, sans-serif;
    width: 100%;
  }
  */
a {
  cursor: pointer;
  cursor: hand;
}
.centertext {
  text-align: center;
}
.fade-enter-active {
  animation: fadeIn .4s;
}
.fade-leave-active {
  animation: fadeOut .4s;
}
.toolbar {
  background: #FFFFFF;
  color: #999ea4;
  -ms-flex-align: center;
      align-items: center;
}
.logo {
  display: inline;
  font-size: 1.7em;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 600;
  vertical-align: middle;
}
.logo i {
  font-size: .5em;
  vertical-align: text-top;
}
.menubtn {
  padding-top: 0.6em !important;
  padding-bottom: 0.6em !important;
}
.title {
  background: #58B7FF;
  color: #fff;
  width: 100%;
  padding: .7em;
  font-size: 1.3em;
}
#header {
  position: fixed;
  z-index: 500;
  width: 100%;
  opacity: .94;
}
#contents {
  margin-top: 0 !important;
  padding-top: 3.7em;
}
#contents .column {
  padding-top: 0 !important;
  padding-right: 0 !important;
}
.page {
  height: 90vh;
}
@media all and (min-width: 0) and (max-width: 480px) {
.toolbar span {
    display: none;
}
}
@media only screen and (min-width: 1200px) {
.ui.container {
    width: 100%;
}
}
@media only screen and (max-width: 767px) {
.ui.container {
    margin: 0 !important;
}
}



